"use client";

export default function Error({ error, reset }: { error: Error; reset: () => void }) {
  return (
    <div className="min-h-screen bg-white text-[#184e77]">
      <div className="mx-auto flex max-w-3xl flex-col gap-6 px-6 py-16">
        <div className="glass-panel p-6">
          <h1 className="text-2xl font-semibold">Something went wrong</h1>
          <p className="mt-2 text-sm text-[#184e77]/70">
            An unexpected error occurred while rendering this page.
          </p>
          <pre className="mt-4 overflow-auto rounded-lg border border-[#52b69a]/30 bg-white/70 p-3 text-xs text-[#184e77]/80">
            {error.message}
          </pre>
          <button
            type="button"
            onClick={reset}
            className="mt-5 rounded-full border border-[#52b69a]/60 px-4 py-2 text-sm text-[#184e77] hover:border-[#52b69a]"
          >
            Try again
          </button>
        </div>
      </div>
    </div>
  );
}

